import styled from "styled-components";
import { Element } from "react-scroll";

// Components
import { Col, Container, Row } from "react-bootstrap";
import { Title } from "./globalStyledComponents";
import paper_header from "../images/Paper_header.PNG";
import poster from "../images/poster.PNG";

const StyledAboutMe = styled.section`
  p {
    font-size: 1.25rem;
  }
  .img {
    width: 18rem;
    height: 18rem;
  }
`;
const DownloadButton = () => {
  const handleDownload = () => {
    // Assuming your PDF file is named "example.pdf" in the "pdfs" subfolder
    const pdfFilePath = process.env.PUBLIC_URL + '/Grundprojekt_synthetic-mobility-data-with-TimeGAN_Kjell_Sophie_censored.pdf';

    // Create a link element
    const link = document.createElement('a');

    // Set the download attribute and the file path
    link.download = 'synthetic-mobility-data-with-TimeGAN.pdf';
    link.href = pdfFilePath;

    // Append the link to the document body
    document.body.appendChild(link);

    // Trigger a click on the link to start the download
    link.click();

    // Remove the link from the document body
    document.body.removeChild(link);
  };

  return (
    <button onClick={handleDownload}>
      Download Paper (PDF)
    </button>
  );
};

   // The current width of the viewport
   const width = window.innerWidth;
   // The width below which the mobile view should be rendered
   const breakpoint = 770;

export default function GetPaper() {

  return (
    width > breakpoint ? 
    (<Element name={"About"} id="about">
      <StyledAboutMe className="section">
        <Container>
          <Container className="d-flex">
            <Title>
              <h2>Get Paper</h2>
              <div className="underline"></div>
            </Title>
          </Container>

          <Row className="align-items-center mt-1">
             <Col className="d-flex flex-column text-center">
               Thank you for attending my poster session on 
                <b>Towards synthethic generation of mobility data with TimeGAN: Evaluation.</b>
              </Col>
            </Row>

            <Container>
            <Row>
              <Col className="d-flex d-md-block mt-3">
                <img
                  src={poster}
                  alt="Poster"
                  className="w-100 mx-auto"
                />
              </Col>
            </Row>
            </Container>

          <Row className="align-items-center mt-5">
            <Col className="d-flex flex-column text-center">
              <Row className="align-items-center mt-5">
              <Container>
                Due to compliance reasons with the training data I am uploading a censored version of the full paper. 
                To get any further information please contact me via <a href="mailto:sophie.schaible@haw-hamburg.de" target="_blank" rel="noreferrer noopener">E-Mail</a> or <a href="https://www.linkedin.com/in/sophie-schaible" target="_blank" rel="noreferrer noopener">LinkedIn</a>. Looking forward!
              </Container>
              </Row>
            </Col>
            
          <Col className="d-flex flex-column text-center">
            <img
              src={paper_header}
              alt="Paper Header"
              className="w-75 mx-auto"
            />
          </Col>
          </Row>
        </Container>
      
        <Row className="align-items-center mt-3 w-50"><DownloadButton /></Row>

      </StyledAboutMe>
    </Element>)

    :

    (<Element name={"About"} id="about">
    <StyledAboutMe className="section">
      <Container>
        <Container className="d-flex">
          <Title>
            <h2>Get Paper</h2>
            <div className="underline"></div>
          </Title>
        </Container>

            <Row className="align-items-center mt-1">
             <Col className="d-flex flex-column text-center">
               Thank you for attending my poster session on 
                <b>Towards synthethic generation of mobility data with TimeGAN: Evaluation.</b>
              </Col>
            </Row>

            <Container>
            <Row>
              <Col className="d-flex d-md-block mt-3">
                <img
                  src={poster}
                  alt="Poster"
                  className="w-100 mx-auto"
                />
              </Col>
            </Row>
            </Container>

        <Row className="align-items-center mt-4">
          <Col className="d-flex flex-column text-center">
            <Row className="align-items-center mt-4">
            <Container>
            Due to compliance reasons with the training data I am uploading a censored version of the full paper. 
              To get any further information please contact me via <a href="mailto:sophie.schaible@haw-hamburg.de" target="_blank" rel="noreferrer noopener">E-Mail</a> or <a href="https://www.linkedin.com/in/sophie-schaible" target="_blank" rel="noreferrer noopener">LinkedIn</a>. Looking forward!
            </Container>
            </Row>
          </Col>
        </Row>
           
          <Row>
            <Col className="d-flex d-md-block mt-3">
              <img
                src={paper_header}
                alt="Paper Abstract"
                className="w-75 mx-auto"
              />
            </Col>
          </Row>

      </Container>
      
      <Row className="align-items-center mt-3 w-50"><DownloadButton /></Row>
    </StyledAboutMe>
  </Element>)

  );
}
