import styled from "styled-components";
// Components
import SocialLinks from "./SocialLinks";
import { Col, Container, Row } from "react-bootstrap";

const StyledFooter = styled.footer`
  min-height: var(--min-footer-height);
  background: var(--primary);

  a {
    color: #45413c;

    &:hover {
      color: #fbfdff;
    }
  }
`;

export default function Footer() {
  return (
    <StyledFooter className="d-flex align-items-center justify-content-center pt-1">
      <Container>
        <Row className="d-flex align-items-center justify-content-center pt-1">
          <Col>
            <SocialLinks />
          </Col>
          <Col>
            <p style={{fontSize: "12px"}}>Credits: Website-Template by <a href='https://github.com/mshuber1981/github-react-portfolio-template' target="_blank" rel="noreferrer noopener">mshuber1981</a> on GitHub, Avatar-Image by <a href="https://www.freepik.com/free-vector/cute-girl-hacker-operating-laptop-cartoon-vector-icon-illustration-people-technology-isolated-flat_65309450.htm" target="_blank" rel="noreferrer noopener">catalyststuff</a> on Freepik</p>
          </Col>
        </Row>
      </Container>
    </StyledFooter>
  );
}
